<template>
<div>
    
<el-dropdown @command="changeLange">
  <span class="el-dropdown-link">
    {{langList}}<i class="el-icon-arrow-down el-icon--right"></i>
  </span>
  <el-dropdown-menu slot="dropdown" >
    <el-dropdown-item command="tc" v-if="$i18n.locale!='tc'">繁體中文</el-dropdown-item>
    <el-dropdown-item command="en" v-if="$i18n.locale!='en'">English</el-dropdown-item>
  </el-dropdown-menu>
</el-dropdown>
</div>
</template>
<script>
import i18n from '../i18n/i18n'
export default {
    data() {
        return {
            
        }
    },
    methods: {
        changeLange(lang) {
            i18n.locale=lang
            console.log(i18n.locale)
        },
    },
    computed: {
        langList(){
            let langName
            switch(i18n.locale){
                case 'tc':
                langName='繁體中文';
                break;
                case 'en':
                langName='English';
                break;
            }
            return langName
        },
    },
}
</script>